
import { useState, useEffect } from "react";

type Item = {
  id: string;
  name: string;
  product_type: string;
  product_code: string;
};

const TT2025 = () => {
  const [data, setData] = useState<Item[]>([]);
  const [productName, setProductName] = useState("");
  const [productType, setProductType] = useState("");
  const [productCode, setProductCode] = useState("");
  const [selectedItem, setSelectedItem] = useState<Item | null>(null);

  useEffect(() => {
    fetch("http://localhost:3001/tt2025")
      .then((res) => res.json())
      .then((data) => setData(data))
      .catch(console.error);
  }, []);

  const handleInsert = async (e: React.FormEvent) => {
    e.preventDefault();
    const body = { name: productName, product_type: productType, product_code: productCode };
    const res = await fetch("http://localhost:3001/tt2025", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(body),
    });
    const result = await res.json();
    setData((prev) => [...prev, result.item]);
    setProductName(""); setProductType(""); setProductCode("");
  };

  const handleUpdate = async () => {
    if (!selectedItem) return alert("กรุณาเลือกสินค้าที่ต้องการแก้ไข");
    const body = { name: productName, product_type: productType, product_code: productCode };
    await fetch(`http://localhost:3001/tt2025/${selectedItem.id}`, {
      method: "PUT",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(body),
    });
    setData(data.map(d => d.id === selectedItem.id ? { ...d, ...body } : d));
    setProductName(""); setProductType(""); setProductCode(""); setSelectedItem(null);
  };

  const handleDelete = async () => {
    if (!selectedItem) return alert("กรุณาเลือกสินค้าที่ต้องการลบ");
    await fetch(`http://localhost:3001/tt2025/${selectedItem.id}`, { method: "DELETE" });
    setData(data.filter(d => d.id !== selectedItem.id));
    setProductName(""); setProductType(""); setProductCode(""); setSelectedItem(null);
  };

  const handleRowClick = (item: Item) => {
    setSelectedItem(item);
    setProductName(item.name);
    setProductType(item.product_type);
    setProductCode(item.product_code);
  };

  return (
    <div style={{ padding: 20 }}>
      <h1>Test</h1>
      <form style={{ marginBottom: 24 }} onSubmit={handleInsert}>
        <div style={{ marginBottom: 8 }}>
          <label>ชื่อสินค้า: </label>
          <input value={productName} onChange={(e) => setProductName(e.target.value)} />
        </div>
        <div style={{ marginBottom: 8 }}>
          <label>ประเภทสินค้า: </label>
          <input value={productType} onChange={(e) => setProductType(e.target.value)} />
        </div>
        <div style={{ marginBottom: 8 }}>
          <label>รหัสสินค้า: </label>
          <input value={productCode} onChange={(e) => setProductCode(e.target.value)} />
        </div>
        <button type="submit">เพิ่มสินค้า</button>
        <button type="button" onClick={handleUpdate}>แก้ไขสินค้า</button>
        <button type="button" onClick={handleDelete}>ลบสินค้า</button>
      </form>

      <table border={1} cellPadding={8}>
        <thead>
          <tr>
            <th>ID</th>
            <th>ชื่อ</th>
            <th>ประเภท</th>
            <th>รหัส</th>
          </tr>
        </thead>
        <tbody>
          {data.map(item => (
            <tr key={item.id} onClick={() => handleRowClick(item)} style={{ cursor: 'pointer', backgroundColor: selectedItem?.id === item.id ? '#f0f0f0' : 'transparent' }}>
              <td>{item.id}</td>
              <td>{item.name}</td>
              <td>{item.product_type}</td>
              <td>{item.product_code}</td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default TT2025;
